@mixin w {
  @media only screen and (max-width: "1439px") {
    @content; } }
@mixin x {
  @media only screen and (max-width: "1365px") {
    @content; } }
@mixin d {
  @media only screen and (max-width: "1199px") {
    @content; } }
@mixin t {
  @media only screen and (max-width: "1023px") {
    @content; } }
@mixin m {
  @media only screen and (max-width: "767px") {
    @content; } }
@mixin a {
  @media only screen and (max-width: "666px") {
    @content; } }
@mixin s {
  @media only screen and (max-width: "474px") {
    @content; } }

@import url('https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Rubik:wght@300;400;500;700&display=swap');
@mixin fr {
  font-family: 'Rubik', sans-serif; }
@mixin fa {
  font-family: 'Abril Fatface', cursive; }

$wh: #fff;
$bl: #000;
$dark: #373981;
$gray: #868E9C;

html {
  // height: 100%
  &.no-scroll {
    overflow: hidden; } }

body {
  min-width: 375px;
  @include fr;
  font-size: 18px;
  line-height: 1.45;
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: $dark;
  @include t {
    font-size: 16px; }
  &.no-scroll {
    overflow: hidden; } }

.page {
  overflow: hidden; }

.center {
  max-width: 1230px;
  margin: 0 auto;
  padding: 0 30px; }

a.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center; }

.btn {
  min-width: 190px;
  height: 56px;
  @include fr;
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: all .25s;
  @include m {
    height: 48px; }
  &__text,
  .icon {
    display: inline-block;
    vertical-align: middle; }
  .icon {
    &:first-child {
      margin-right: 10px; } }
  .icon-play {
    font-size: 16px; }
  &__font {
    font-weight: 400; }
  &:hover {
    transform: translateY(-2px); }
  &:active {
    transform: translateY(2px); }
  &_border {
    border: 1px solid #1F2549;
    color: $dark;
    .icon {
      fill: $dark; }
    &:hover {
      box-shadow: 0 5px 15px 0 rgba(0,0,0,0.2); }
    &:active {
      box-shadow: 0 3px 7px 0 rgba(0,0,0,0.15); } }
  &_pink {
    background: #FF6D96;
    box-shadow: 0 2px 4px 0 rgba(255,109,150,.25), 0 8px 10px 0 rgba(0,0,0,.1);
    color: $wh;
    &:hover {
      box-shadow: 0 4px 8px 0 rgba(255,109,150,.25), 0 10px 16px 0 rgba(0,0,0,.12); }
    &:active {
      box-shadow: 0 1px 3px 0 rgba(255,109,150,.25), 0 3px 5px 0 rgba(0,0,0,.1); } }
  &_white {
    background: $wh;
    box-shadow: 0 10px 20px 0 rgba(0,0,0,.1);
    color: $dark;
    &:hover {
      box-shadow: 0 15px 30px 0 rgba(0,0,0,.1); }
    &:active {
      box-shadow: 0 7px 14px 0 rgba(0,0,0,.1); } } }

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 20;
  padding: 48px 0;
  @include d {
    padding: 36px 0; }
  @include m {
    padding: 26px 0; }
  &__center {
    display: flex;
    align-items: center;
    max-width: 1884px; }
  &__logo {
    font-size: 0;
    @include t {
      margin-right: auto; }
    @include m {
      z-index: 20; } }
  &__logo &__pic {
    width: 176px;
    @include m {
      width: 160px; } }
  &__wrap {
    display: flex;
    align-items: center;
    flex-grow: 1;
    padding-left: 150px;
    @include x {
      padding-left: 100px; }
    @include d {
      padding-left: 75px; }
    @include t {
      flex-direction: column;
      align-items: stretch;
      position: absolute;
      top: 0;
      right: 0;
      z-index: 10;
      width: 350px;
      min-height: 100vh;
      padding: 100px 50px 0;
      background: rgba(255,255,255,0.98);
      box-shadow: 0 10px 20px 0 rgba(0,0,0,0.0);
      transform: translateX(100%);
      transition: transform .25s;
      &.visible {
        transform: translateX(0); } }
    @include m {
      width: 100%;
      min-height: auto;
      padding-top: 120px;
      text-align: center; } }
  &__nav {
    display: flex;
    margin-right: auto;
    margin-bottom: -3px;
    @include t {
      flex-direction: column;
      margin-bottom: auto; }
    @include m {
      align-items: center;
      width: 100%;
      margin-bottom: auto; }
    @include s {
      margin-bottom: 64px; } }
  &__link {
    font-size: 16px;
    font-weight: 500;
    color: $gray;
    transition: color .25s;
    @include t {
      font-size: 20px; }
    @include m {
      font-size: 18px; }
    &:hover {
      color: $dark; }
    @include nl {
      margin-right: 62px;
      @include d {
        margin-right: 45px; }
      @include t {
        margin: 0 0 50px; }
      @include m {
        margin-bottom: 26px; } } }
  &__btns {
    @include t {
      margin: 50px -50px 0;
      padding: 50px;
      background-image: linear-gradient(90deg, #E97A99 4%, #FAC9B6 96%); }
    @include m {
      margin-top: 0;
      padding: 24px; } }
  &__btn {
    height: 40px; }
  &__burger {
    display: none;
    position: relative;
    z-index: 12;
    width: 30px;
    height: 30px;
    background: none;
    font-size: 0;
    transition: opacity .25s;
    @include t {
      display: inline-block; }
    &:before,
    &:after {
      content: "";
      display: inline-block;
      width: 24px;
      height: 2px;
      background: $wh;
      transition: transform .25s, background .25s;
      @include m {
        background: $dark; } }
    &:before {
      margin-bottom: 2.5px; }
    &:after {
      margin-top: 2.5px; }
    &:hover {
      opacity: .85; }
    &.active {
      &:before,
      &:after {
        background: $dark; }
      &:before {
        transform: translateY(3.5px) rotate(45deg); }
      &:after {
        transform: translateY(-3.5px) rotate(-45deg); } } }
  &__bg {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 8;
    background: rgba($dark, .6);
    visibility: hidden;
    opacity: 0;
    transition: all .25s;
    &.show {
      @include t {
        visibility: visible;
        opacity: 1; } } } }

.main {
  position: relative;
  background-image: linear-gradient(135deg, #FFFFFF 50%, #F7F7F7 100%);
  &__center {
    position: relative;
    z-index: 5;
    display: flex;
    align-items: center;
    min-height: 920px;
    padding-top: 100px;
    padding-bottom: 115px;
    @include w {
      min-height: 820px; }
    @include d {
      min-height: 768px; }
    @include m {
      min-height: auto;
      padding-top: 125px;
      padding-bottom: 60px; } }
  &__wrap {
    max-width: 670px;
    @include t {
      max-width: 470px; }
    @include m {
      max-width: 100%;
      margin: 0 auto;
      text-align: center; } }
  &__brand {
    @include fa;
    font-size: 160px;
    line-height: 1;
    letter-spacing: 2px;
    font-weight: 400;
    color: rgba($dark, .05);
    @include w {
      font-size: 130px; }
    @include d {
      font-size: 120px; }
    @include t {
      font-size: 100px; }
    @include m {
      font-size: 66px;
      line-height: 1.2;
      letter-spacing: 1px; }
    &[data-aos] {
      transform: translateY(40px) scaleY(1.4);
      opacity: 0;
      color: rgba($dark, .65);
      transition-property: transform, opacity, color;
      &.aos-animate {
        transform: translateY(0) scaleY(1);
        opacity: 1;
        color: rgba($dark, .05); } } }
  &__title {
    margin-bottom: 20px;
    @include fa;
    font-size: 80px;
    letter-spacing: 2px;
    line-height: 1;
    font-weight: 400;
    @include w {
      font-size: 74px; }
    @include d {
      font-size: 66px; }
    @include t {
      font-size: 56px; }
    @include m {
      margin: -7px 0 10px;
      font-size: 46px;
      letter-spacing: 1px;
      line-height: 1.3; }
    &[data-aos] {
      transform: translateY(40px) scaleY(1.4);
      opacity: 0;
      color: #FF6D96;
      transition-property: transform, opacity, color;
      &.aos-animate {
        transform: translateY(0) scaleY(1);
        opacity: 1;
        color: $dark; } } }
  &__info {
    max-width: 470px;
    margin-bottom: 48px;
    color: $gray;
    @include m {
      margin: 0 auto 32px; } }
  &__btns {
    display: flex;
    @include m {
      flex-direction: column;
      align-items: center; } }
  &__btn {
    @include nl {
      margin-right: 16px;
      @include m {
        margin: 0 0 16px; } } }
  &__bg {
    position: absolute;
    top: 0;
    right: 0;
    left: calc(50% + 215px);
    bottom: 0;
    @include t {
      left: calc(50% + 185px); }
    @include m {
      position: relative;
      top: auto;
      right: auto;
      left: auto;
      bottom: auto;
      height: 340px; } }
  &__bg &__box {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 0;
    margin-left: auto;
    background-image: linear-gradient(180deg, #E97A99 6%, #FAC9B6 99%);
    font-size: 0;
    animation: bg-width 1s forwards; }
  &__preview {
    position: absolute;
    left: -334px;
    bottom: -204px;
    pointer-events: none;
    animation: image 1s .75s forwards;
    transform: translateX(150px);
    opacity: 0;
    @include w {
      bottom: -140px; }
    @include d {
      left: -280px;
      bottom: -80px; }
    @include t {
      left: -220px;
      bottom: -30px; }
    @include m {
      top: -32px;
      left: 37%;
      margin-left: -50%;
      bottom: auto; } }
  &__preview &__pic {
    @include w {
      width: 600px; }
    @include d {
      width: 500px; }
    @include t {
      width: 450px; }
    @include m {
      width: 380px; } } }

@keyframes bg-width {
  100% {
    width: 100%; } }

@keyframes image {
  80% {
    opacity: 1; }
  100% {
    transform: translateX(0);
    opacity: 1; } }

.title {
  @include fa;
  font-size: 60px;
  font-weight: 400;
  line-height: (80/60);
  letter-spacing: 2px;
  @include w {
    font-size: 54px; }
  @include t {
    font-size: 44px; }
  @include m {
    font-size: 36px;
    letter-spacing: 1px; }
  &_sm {
    font-size: 40px;
    line-height: (60/40);
    letter-spacing: 1px;
    @include w {
      font-size: 36px; }
    @include t {
      font-size: 30px; }
    @include m {
      font-size: 28px; } }
  &[data-aos] {
    transform: translateY(40px) scaleY(1.4);
    opacity: 0;
    color: #FF6D96;
    transition-property: transform, opacity, color;
    &.aos-animate {
      transform: translateY(0) scaleY(1);
      opacity: 1;
      color: $dark; } } }

.about {
