        padding-left: 30px;
        @include w {
          padding-right: 70px; }
        @include t {
          padding-right: 55px; }
        @include m {
          max-width: 100%;
          padding: 0 30px; } }
      .about__details {
        &[data-aos] {
          transform: translateX(-60px) skewX(20deg);
          opacity: 0;
          transition-property: transform, opacity;
          &.aos-animate {
            transform: translateX(0) skewX(0deg);
            opacity: 1; } } }
      .about__preview {
        margin-left: -100px;
        @include d {
          margin-left: -50px; }
        @include m {
          margin: 0 auto -30px; }
        @include s {
          margin: 0 0 -30px; }
        &[data-aos] {
          transform: translateX(60px) skewX(-20deg);
          opacity: 0;
