      align-items: center;
      max-width: 190px;
      margin-top: 32px; }
    @include m {
      margin: 32px auto 0; } }
  &__btn {
    margin-right: 40px;
    @include t {
      margin: 0 0 16px; } }
  &__link {
    position: relative;
    padding: 7px 0;
    font-size: 12px;
    font-weight: 700;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: $dark;
    &:before {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
