     width: 0; } } }

.gallery {
  &__slide {
    @include clr; }
  &__preview {
    font-size: 0;
    &:first-child,
    &:nth-child(3) {
      float: left;
      width: calc(100%/3); }
    &:nth-child(2) {
      float: right;
      width: calc(100%*2/3); }
    &[data-aos] {
      transform: translateY(60px) scaleY(1.3);
      opacity: 0;
      transition-property: transform, opacity;
      &.aos-animate {
        transform: translateY(0) scaleY(1);
        opacity: 1; } } }
  &__slider {
    position: relative;
    &:before,
    &:after {
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      z-index: 2;
      width: 240px;
      transition: opacity .4s;
      @include x {
        width: 210px; }
      @include m {
        display: none; } }
    &:before {
      left: 0;
      background-image: linear-gradient(-90deg, rgba(0,0,0,0.00) 3%, rgba(0,0,0,0.20) 100%); }
    &:after {
      right: 0;
      background-image: linear-gradient(90deg, rgba(0,0,0,0.00) 0%, rgba(0,0,0,0.20) 97%); }
    &[data-aos] {
      &:before,
