    width: 60px;
    @include m {
      width: 45px; } }
  &__menu {
    display: flex;
    justify-content: center;
    margin-bottom: 36px;
    @include m {
      flex-direction: column;
      align-items: center;
      justify-content: stretch; } }
  &__link {
    font-size: 16px;
    line-height: 1.2;
    color: $gray;
    transition: color .25s;
    &:hover {
      color: $dark; }
    @include nl {
      margin-right: 64px;
      @include m {
        margin: 0 0 24px; } } }
  &__social {
    margin-bottom: 52px;
    @include m {
      margin-bottom: 36px; } }
  &__copyright {
    text-align: center;
    font-size: 12px;
    line-height: (14/12);
    color: $gray; } }

[data-aos="animation-scale-y"] {
  transform: translateY(40px) scaleY(1.3);
  opacity: 0;
  transition-property: transform, opacity;
  &.aos-animate {
    transform: translateY(0) scaleY(1);
    opacity: 1; } }



